
/**
 * SourceLocation
 */
export class SourceLocation {
  /**
   * constructor
   * @param  {} p []
   * @return {}   []
   */
  constructor(p = {}, start, end) {
    this.start = start;
    this.end = end;
    this.source = '';
    if (p.sourceFile !== null) {
      this.source = p.sourceFile;
    }
  }
}

/**
 * token
 */
export default class Token {
  /**
   * constructor
   * @param  {} p []
   * @return {}   []
   */
  constructor(p = {}){
    this.type = p.type;
    this.value = p.value;
    this.start = p.start;
    this.end = p.end;
    this.loc = new SourceLocation(p, p.startLoc, p.endLoc);
    this.range = [p.start, p.end];
  }
}